<template>
  <div v-clickoutside:outside="closebar" @click="closeRight">
    <el-dialog
      title="选择移动位置"
      :visible.sync="dialogFormVisible"
      width="600px"
      :show-close="false"
      class="moveDialog"
      ><div class="folderTree">
        <el-tree
          :data="moveFolderData"
          :props="defaultProps"
          accordion
          node-key="id"
          default-expand-all
          :expand-on-click-node="false"
          @node-click="handleNodeClick"
        ></el-tree>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button size="mini" @click="moveFileNewFolder()" style="float: left"
          >新建文件夹</el-button
        >
        <el-button size="mini" @click="dialogMoveFileCancel()">取 消</el-button>
        <el-button size="mini" type="primary" @click="dialogMoveFileOk()"
          >确 定</el-button
        >
      </div>
    </el-dialog>
    <context-menu
      class="right-menu"
      :target="contextMenuTarget"
      :style="{
        display: contextMenuVisible === true ? 'block' : 'none',
        top: mouse.y,
        left: mouse.x,
      }"
    >
      <a
        :href="
          clickFile.type === '.folder'
            ? 'javascript:void(0);'
            : getFile(clickFile)
        "
        :download="clickFile.file_name"
        @click="handleDownload()"
        v-if="clickFile.type !== '.folder'"
        >下载</a
      >
      <a
        href="javascript:;"
        @click="handleShare(clickFile.id, clickFile)"
        v-if="clickFile.type !== '.folder'"
        >分享</a
      >
      <a
        href="javascript:;"
        @click="moveFileBox(clickFile.id, clickFile)"
        v-if="clickFile.type !== '.folder'"
        >移动</a
      >
      <a href="javascript:;" @click="reFileNameBox(clickFile.id)">重命名</a>
      <a href="javascript:;" @click="handleDelete(clickFile.id, clickFile)"
        >删除</a
      >
    </context-menu>
    <div class="fixedBox">
      <div class="leftNav">
        <div
          :class="{
            leftNavBtn: true,
            leftNavBtnClick: this.fileTypeClick === '全部',
          }"
          @click="findType('全部')"
        >
          <i class="el-icon-folder"></i>&nbsp;&nbsp;全部
        </div>
        <div
          :class="{
            leftNavBtn: true,
            leftNavBtnClick: this.fileTypeClick === '图片',
          }"
          @click="findType('图片')"
        >
          <i class="el-icon-picture-outline"></i>&nbsp;&nbsp;图片
        </div>
        <div
          :class="{
            leftNavBtn: true,
            leftNavBtnClick: this.fileTypeClick === '视频',
          }"
          @click="findType('视频')"
        >
          <i class="el-icon-film"></i>&nbsp;&nbsp;视频
        </div>
        <div
          :class="{
            leftNavBtn: true,
            leftNavBtnClick: this.fileTypeClick === '音乐',
          }"
          @click="findType('音乐')"
        >
          <i class="el-icon-service"></i>&nbsp;&nbsp;音乐
        </div>
        <div
          :class="{
            leftNavBtn: true,
            leftNavBtnClick: this.fileTypeClick === '文档',
          }"
          @click="findType('文档')"
        >
          <i class="el-icon-tickets"></i>&nbsp;&nbsp;文档
        </div>
        <div
          :class="{
            leftNavBtn: true,
            leftNavBtnClick: this.fileTypeClick === '压缩',
          }"
          @click="findType('压缩')"
        >
          <i class="el-icon-box"></i>&nbsp;&nbsp;压缩
        </div>
      </div>
      <div class="navBar">
        <div class="navB">
          <el-breadcrumb separator="/">
            <el-breadcrumb-item v-for="(item, index) in path" :key="index"
              ><a @click="getPath(item, index)">{{ item }}</a>
            </el-breadcrumb-item>
            <el-breadcrumb-item>
              {{ nowfolder }}
            </el-breadcrumb-item>
          </el-breadcrumb>
        </div>
        <div class="view">
          <el-radio-group v-model="listType"
            ><span class="viewTip">视图：</span>
            <el-radio label="平铺"></el-radio>
            <el-radio label="列表"></el-radio>
          </el-radio-group>
        </div>
      </div>
    </div>
    <div class="fileBox">
      <el-table
        v-if="listType === '列表'"
        :height="height"
        :data="tableData"
        :cell-style="{ 'text-align': 'center' }"
        :header-cell-style="{ 'text-align': 'center' }"
        style="width: 1106px; margin-left: 158px; margin-top: -50px"
      >
        <el-table-column type="index"> </el-table-column>
        <el-table-column prop="file_name" label="文件名" width="180px">
          <template slot-scope="scope">
            <el-button
              :disabled="scope.row.type !== '.folder'"
              @click.native.prevent="gotofolder(scope.row)"
              type="text"
              size="small"
            >
              {{ scope.row.file_name }}
            </el-button>
          </template>
        </el-table-column>
        <el-table-column
          prop="size"
          label="文件大小"
          width="180px"
          :formatter="dealSize"
        >
        </el-table-column>
        <el-table-column
          prop="upload_time"
          label="上传时间"
          width="180px"
          :formatter="dealTime"
        >
        </el-table-column>
        <el-table-column prop="download" label="下载次数" width="180px">
        </el-table-column>
        <el-table-column label="操作" min-width="330px">
          <template slot-scope="scope">
            <a
              :href="
                scope.row.type === '.folder'
                  ? 'javascript:void(0);'
                  : getFile(scope.row)
              "
              :download="scope.row.file_name"
            >
              <el-button
                size="mini"
                type="success"
                @click="handleDownload"
                :disabled="scope.row.type === '.folder'"
                >下载</el-button
              >
            </a>
            <el-button
              size="mini"
              type="primary"
              @click="handleShare(scope.$index, scope.row)"
              :disabled="scope.row.type === '.folder'"
              >分享</el-button
            >
            <el-button
              size="mini"
              type="warning"
              @click="moveFileBox(scope.row.id, scope.row)"
              :disabled="scope.row.type === '.folder'"
              >移动</el-button
            >
            <el-button
              size="mini"
              type="info"
              @click="reFileNameBox(scope.row.id)"
              >重命名</el-button
            >
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.$index, scope.row)"
              >删除</el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <div class="listpp" v-if="listType === '平铺'">
        <div class="infoBox outside">
          <div class="infoBar outside" v-if="barFlag === 1">
            <p class="fs outside" v-if="clickFile.type !== '.folder'">
              文件大小：
              {{ clickFile.filesize }}
            </p>
            <p class="ft outside">
              上传时间：
              {{ clickFile.upload_time }}
            </p>
            <p class="fd outside" v-if="clickFile.type !== '.folder'">
              下载次数：
              {{ clickFile.download }}
            </p>
            <div class="fb outside">
              <a
                :href="
                  clickFile.type === '.folder'
                    ? 'javascript:void(0);'
                    : getFile(clickFile)
                "
                :download="clickFile.file_name"
              >
                <el-button
                  size="mini"
                  type="success"
                  @click="handleDownload"
                  :disabled="clickFile.type === '.folder'"
                  >下载</el-button
                >
              </a>
              <el-button
                size="mini"
                type="primary"
                @click="handleShare(clickFile.id, clickFile)"
                :disabled="clickFile.type === '.folder'"
                >分享</el-button
              >
              <el-button
                size="mini"
                type="warning"
                @click="moveFileBox(clickFile.id, clickFile)"
                :disabled="clickFile.type === '.folder'"
                >移动</el-button
              >
              <el-button
                size="mini"
                type="info"
                @click="reFileNameBox(clickFile.id)"
                >重命名</el-button
              >
              <el-button
                size="mini"
                type="danger"
                @click="handleDelete(clickFile.id, clickFile)"
                >删除</el-button
              >
            </div>
          </div>
        </div>
        <div
          :class="{
            fileListpp: true,
            fileListppClick: item.id === clickFile.id,
          }"
          v-for="item in tableData"
          :key="item.id"
        >
          <!-- 文件类型为MP3 -->
          <div
            class="clickBox"
            @click="infoBar(item)"
            @contextmenu.prevent="showRight(item)"
            @dblclick="dialogMp3Box(item)"
            @mouseup="showPopup"
            v-if="judgType(item.type) === 'MP3'"
          >
            <div class="fileIcon outside">
              <img
                class="icon"
                width="256"
                height="256"
                :src="getIconType(item)"
                :alt="item.file_name"
              />
            </div>
            <p style="-webkit-box-orient: vertical" class="fileName">
              {{ item.file_name }}
            </p>
          </div>

          <!-- 文件类型为MP4 -->
          <div
            class="clickBox"
            @click="infoBar(item)"
            @contextmenu.prevent="showRight(item)"
            @dblclick="dialogMovieBox(item)"
            @mouseup="showPopup"
            v-if="judgType(item.type) === 'MP4'"
          >
            <div class="fileIcon outside">
              <img
                class="icon"
                width="256"
                height="256"
                :src="getIconType(item)"
                :alt="item.file_name"
              />
            </div>
            <p style="-webkit-box-orient: vertical" class="fileName">
              {{ item.file_name }}
            </p>
          </div>

          <!-- 文件类型为文本文档 -->
          <div
            class="clickBox"
            @click="infoBar(item)"
            @contextmenu.prevent="showRight(item)"
            @dblclick="dialogTXTBox(item)"
            @mouseup="showPopup"
            v-if="judgType(item.type) === 'TEXT'"
          >
            <div class="fileIcon outside">
              <img
                class="icon"
                width="256"
                height="256"
                :src="getIconType(item)"
                :alt="item.file_name"
              />
            </div>
            <p style="-webkit-box-orient: vertical" class="fileName">
              {{ item.file_name }}
            </p>
          </div>

          <!-- 文件类型为IMG -->
          <div
            class="clickBox"
            @click="infoBar(item)"
            @contextmenu.prevent="showRight(item)"
            @dblclick="dialogImageBox(item)"
            @mouseup="showPopup"
            v-if="judgType(item.type) === 'img'"
          >
            <div class="fileIcon outside">
              <img
                class="icon"
                width="256"
                height="256"
                :src="getIconType(item)"
                :alt="item.file_name"
              />
            </div>
            <p style="-webkit-box-orient: vertical" class="fileName">
              {{ item.file_name }}
            </p>
          </div>

          <!-- 文件类型为其他 -->
          <div
            class="clickBox"
            @click="infoBar(item)"
            @contextmenu.prevent="showRight(item)"
            @dblclick="dialogMessageBox()"
            @mouseup="showPopup"
            v-if="judgType(item.type) === '其他'"
          >
            <div class="fileIcon outside">
              <img
                class="icon"
                width="256"
                height="256"
                :src="getIconType(item)"
                :alt="item.file_name"
              />
            </div>
            <p style="-webkit-box-orient: vertical" class="fileName">
              {{ item.file_name }}
            </p>
          </div>

          <!-- 文件类型为文件夹 -->
          <div
            class="clickBox"
            @click="infoBar(item)"
            @contextmenu.prevent="showRight(item)"
            @dblclick="gotofolder(item)"
            @mouseup="showPopup"
            v-if="judgType(item.type) === 'folder'"
          >
            <div class="fileIcon outside">
              <img
                class="icon"
                width="256"
                height="256"
                :src="getIconType(item)"
                :alt="item.file_name"
              />
            </div>
            <p style="-webkit-box-orient: vertical" class="fileName">
              {{ item.file_name }}
            </p>
          </div>
        </div>
        <div class="fileListpp" v-if="fileTypeClick === '全部'">
          <div class="clickBox" @click="showUpAlert()">
            <div class="fileIcon">
              <img
                class="icon"
                width="256"
                height="256"
                :src="`${hostUrl}/icon/up.png`"
                alt="上传文件"
              />
            </div>
            <p style="-webkit-box-orient: vertical" class="fileName">
              上传文件
            </p>
          </div>
        </div>
      </div>
    </div>
    <fileUpload
      ref="upFile"
      :dialogVisible="ifShowUpload"
      @reGetFileList="reGetFileList"
    ></fileUpload>
  </div>
</template>  

<script>
import Clickoutside from "element-ui/src/utils/clickoutside";
import fileUpload from "./file-upload";
export default {
  directives: { Clickoutside },
  components: { fileUpload },
  data() {
    return {
      moveFolderData: [
        {
          label: "我的网盘",
          path: "/",
          children: [],
        },
      ],
      defaultProps: {
        children: "children",
        label: "label",
      },
      tableData: [],
      listType: "平铺",
      barFlag: 2,
      clickFile: {},
      path: [],
      nowfolder: "我的网盘",
      newFilePath: "我的网盘",
      fileTypeClick: "全部",
      addfolderName: "",
      needMoveFileId: null,
      uid: 0,
      ifShowUpload: false,
      upPath: "",
      contextMenuTarget: document.querySelector(".listpp"), //绑定的dom
      contextMenuVisible: false,
      dialogFormVisible: false,
      mouse: {
        x: "0px",
        y: "0px",
      },
      fullHeight: document.documentElement.clientHeight,
      height: document.documentElement.clientHeight - 290,
      clickFolderData: null,
      hostUrl: "",
    };
  },
  watch: {
    fullHeight(val) {
      if (!this.timer) {
        this.fullHeight = val;
        this.timer = true;
        let that = this;
        setTimeout(function () {
          that.timer = false;
        }, 400);
      }
      this.height = this.fullHeight - 290;
    },
  },
  methods: {
    judgType(type) {
      if (type.toLowerCase() === ".folder") {
        return "folder";
      } else if (
        type.toLowerCase() === ".jpg" ||
        type.toLowerCase() === ".jpeg" ||
        type.toLowerCase() === ".png" ||
        type.toLowerCase() === ".tiff" ||
        type.toLowerCase() === ".bmp" ||
        type.toLowerCase() === ".gif" ||
        type.toLowerCase() === ".ico"
      ) {
        return "img";
      } else if (
        type.toLowerCase() === ".txt" ||
        type.toLowerCase() === ".html" ||
        type.toLowerCase() === ".css" ||
        type.toLowerCase() === ".js" ||
        type.toLowerCase() === ".log" ||
        type.toLowerCase() === ".json" ||
        type.toLowerCase() === ".url" ||
        type.toLowerCase() === ".vue" ||
        type.toLowerCase() === ".xml"
      ) {
        return "TEXT";
      } else if (type.toLowerCase() === ".mp3") {
        return "MP3";
      } else if (type.toLowerCase() === ".mp4") {
        return "MP4";
      } else {
        return "其他";
      }
    },
    dialogTXTBox(item) {
      this.$alert(
        `<div style="width:570px;height:750px;overflow:auto;"><pre class="txt${item.id}" style="margin:315px auto;">加载中......</pre>`,
        `${item.file_name}`,
        {
          dangerouslyUseHTMLString: true,
          showConfirmButton: false,
          customClass: "messageTextBoxClass",
          closeOnClickModal: false,
          lockScroll: false,
          callback: (action) => {},
        }
      );
      let src = `${this.hostUrl}/file/download/${item.id}/${item.hash_name}`;
      this.$http
        .get(src)
        .then((res) => {
          let txt = document.querySelector(`.txt${item.id}`);
          txt.style.margin = "0 auto";
          txt.innerHTML = `<xmp>${res.data}</xmp>`;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    dialogMovieBox(item) {
      this.$alert(
        `<video class="movie${item.id}" src="${this.hostUrl}/file/download/${item.id}/${item.hash_name}" style="margin:0 auto;width:1070px;height:600px;" controls="controls" autoplay=true;>您的浏览器不支持在线播放！请先下载该资源！</video>`,
        `${item.file_name}`,
        {
          dangerouslyUseHTMLString: true,
          showConfirmButton: false,
          customClass: "messageMp4BoxClass",
          closeOnClickModal: false,
          lockScroll: false,
          callback: (action) => {
            if (action === "cancel") {
              document.querySelector(`.movie${item.id}`).pause();
            }
          },
        }
      );
    },
    dialogMp3Box(item) {
      this.$alert(
        `<audio class="music${item.id}" src="${this.hostUrl}/file/download/${item.id}/${item.hash_name}" style="margin:0 auto;width:470px;" controls="controls" autoplay=true;>您的浏览器不支持在线播放！请先下载该资源！</audio>`,
        `${item.file_name}`,
        {
          dangerouslyUseHTMLString: true,
          showConfirmButton: false,
          customClass: "messageMp3BoxClass",
          closeOnClickModal: false,
          lockScroll: false,
          callback: (action) => {
            if (action === "cancel") {
              document.querySelector(`.music${item.id}`).pause();
            }
          },
        }
      );
    },
    dialogImageBox(item) {
      this.$alert(
        `<img src="${this.hostUrl}/file/download/${item.id}/${item.hash_name}" alt="${item.file_name}" style="margin:0 auto;width:1170px;height:600px;object-fit: scale-down;">`,
        `${item.file_name}`,
        {
          dangerouslyUseHTMLString: true,
          showConfirmButton: false,
          customClass: "messageBoxClass",
          closeOnClickModal: true,
          lockScroll: false,
          callback: (action) => {},
        }
      );
    },
    dialogMessageBox() {
      this.$alert("请下载后查看！", "提示", {
        confirmButtonText: "确定",
        lockScroll: false,
        callback: (action) => {},
      });
    },
    findType(type) {
      this.fileTypeClick = type;
      this.path = [];
      this.closebar();
      this.nowfolder = "我的网盘";
      if (type === "全部") {
        this.getFileList("我的网盘");
        this.$refs.upFile.setPath("我的网盘");
      } else {
        let params = {
          uid: sessionStorage.getItem("uid"),
          type: type,
        };
        this.$http
          .post(`${this.hostUrl}/file/fileType`, params)
          .then((res) => {
            if (res.data.code === 0) {
              this.$message.error(res.data.msg);
            } else {
              this.tableData = res.data;
            }
          })
          .catch((err) => {
            console.log(err);
          });
      }
    },
    moveFileNewFolder() {
      this.$prompt("请输入文件夹名称", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
      })
        .then(({ value }) => {
          if (value) {
            this.addfolder({
              file_name: value,
              path: this.newFilePath,
              uid: this.uid,
              hash_name: "folder" + new Date().getTime(),
            });
            this.handleNodeClick(this.clickFolderData);
          } else {
            this.$message.error("文件名禁止为空");
          }
        })
        .catch(() => {});
    },
    handleNodeClick(data) {
      if (data) {
        this.clickFolderData = data;
        let path = "";
        if (data.path === "/") {
          path = data.label;
        } else {
          path = data.path;
        }
        this.getfolderList(path, data);
      } else {
        this.$message.error("请先选择文件夹！");
      }
    },
    getfolderList(path, data) {
      this.newFilePath = path;
      let params = {
        uid: sessionStorage.getItem("uid"),
        path: path,
      };
      this.$http
        .post(`${this.hostUrl}/file/list`, params)
        .then((res) => {
          if (res.data.code === 0) {
            this.$message.error(res.data.msg);
          } else {
            let fileList = res.data;
            let newFolderData = [];
            for (let i = 0; i < fileList.length; i++) {
              if (fileList[i].type === ".folder") {
                newFolderData.push({
                  label: fileList[i].file_name,
                  path: fileList[i].path + "/" + fileList[i].file_name,
                  children: [],
                });
              }
              data.children = newFolderData;
            }
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    getIconType(item) {
      let type = item.type.replace(/./, "").toLowerCase();
      let iconType = "";
      let test = /^aep$|^ai$|^avi$|^cdr$|^css$|^doc$|^docx$|^dwg$|^eps$|^exe$|^folder$|^gif$|^html$|^ipa$|^iso$|^jpeg$|^jpg$|^js$|^json$|^log$|^mov$|^mp3$|^mp4$|^new$|^pdf$|^php$|^png$|^ppt$|^pptx$|^psd$|^rar$|^ttf$|^txt$|^url$|^vue$|^xls$|^xlsx$|^zip$|^bmp$|^raw$|^tiff$|^xml$/;
      if (test.test(type)) {
        iconType = `${this.hostUrl}/icon/${type}.png`;
      } else {
        iconType = `${this.hostUrl}/icon/unk.png`;
      }
      return iconType;
    },
    reGetFileList(path) {
      this.ifShowUpload = false;
      this.refreshFileList(path);
      this.closebar();
    },
    addfolder(addfolderName) {
      if (addfolderName !== "") {
        this.$http
          .post(`${this.hostUrl}/file/addfolder`, addfolderName)
          .then((res) => {
            if (res.data.flag === 0) {
              this.$message.error(res.data.msg);
            } else {
              this.$message.success(res.data.msg);
              let path = "";
              if (this.path.length === 0) {
                path = this.nowfolder;
              } else {
                path = this.path.join("/") + "/" + this.nowfolder;
              }
              this.$refs.upFile.setPath(path);
              this.refreshFileList(path);
              this.closebar();
            }
          })
          .catch((err) => {
            console.log(err);
          });
      } else {
        this.$message.error("文件名禁止为空");
      }
    },
    showUpAlert() {
      this.ifShowUpload = false;
      this.$confirm("选择建立文件夹或上传文件", "提示", {
        distinguishCancelAndClose: true,
        confirmButtonText: "上传文件",
        cancelButtonText: "新建文件夹",
        type: "info",
        center: true,
      })
        .then(() => {
          this.ifShowUpload = true;
        })
        .catch((action) => {
          this.ifShowUpload = false;
          if (action !== "cancel") {
            return;
          } else {
            this.$prompt("请输入文件夹名称", "提示", {
              confirmButtonText: "确定",
              cancelButtonText: "取消",
            })
              .then(({ value }) => {
                if (value) {
                  let upPath = "";
                  if (this.path.length === 0) {
                    upPath = this.nowfolder;
                  } else {
                    upPath = this.path.join("/") + "/" + this.nowfolder;
                  }
                  this.addfolder({
                    file_name: value,
                    path: upPath,
                    uid: this.uid,
                    hash_name: "folder" + new Date().getTime(),
                  });
                } else {
                  this.$message.error("文件名禁止为空");
                }
              })
              .catch(() => {});
          }
        });
    },
    getPath(item, index) {
      let num = this.path.length;
      this.path.splice(index, num - index);
      this.nowfolder = item;
      let path = "";
      if (this.path.length === 0) {
        path = this.nowfolder;
      } else {
        path = this.path.join("/") + "/" + this.nowfolder;
      }
      this.$refs.upFile.setPath(path);
      this.refreshFileList(path);
      this.closebar();
    },
    gotofolder(item) {
      this.path = item.path.split("/");
      this.nowfolder = item.file_name;
      let path = "";
      if (this.path.length === 0) {
        path = this.nowfolder;
      } else {
        path = this.path.join("/") + "/" + this.nowfolder;
      }
      this.$refs.upFile.setPath(path);
      this.refreshFileList(path);
      this.closebar();
    },
    closebar() {
      this.barFlag = 2;
      this.clickFile = {};
    },
    infoBar(item) {
      this.barFlag = 1;
      this.clickFile = item;
      this.clickFile.filesize = this.dealSize({ size: item.size * 1 });
      this.clickFile.downloadUrl = `${this.hostUrl}/file/download/${item.id}/${item.hash_name}`;
      this.contextMenuVisible = false;
    },
    showRight(item) {
      this.barFlag = 1;
      this.clickFile = item;
      this.clickFile.filesize = this.dealSize({ size: item.size * 1 });
      this.clickFile.downloadUrl = `${this.hostUrl}/file/download/${item.id}/${item.hash_name}`;
      this.contextMenuVisible = true;
    },
    closeRight() {
      this.contextMenuVisible = false;
    },
    showPopup(e) {
      this.mouse.x = e.clientX + 1 + "px";
      this.mouse.y = e.clientY + 1 + "px";
    },
    moveFileBox(id, file) {
      //弹窗 选择文件夹
      this.needMoveFileId = id;
      this.dialogFormVisible = true;
    },
    dialogMoveFileCancel() {
      //清空输入内容 关闭窗口
      this.needMoveFileId = null;
      this.newFilePath = "我的网盘";
      this.dialogFormVisible = false;
      this.moveFolderData = [
        {
          label: "我的网盘",
          path: "/",
          children: [],
        },
      ];
      this.clickFolderData = null;
    },
    dialogMoveFileOk() {
      //路径为空
      if (this.newFilePath === "") {
        this.$message({
          type: "error",
          message: `路径不能为空`,
        });
      } else {
        //向后台发送信息
        //(id file newPath)
        this.moveFile();
        this.moveFolderData = [
          {
            label: "我的网盘",
            path: "/",
            children: [],
          },
        ];
      }
    },
    moveFile() {
      let params = {
        id: this.needMoveFileId,
        newPath: this.newFilePath,
        uid: this.uid,
      };
      this.$http
        .post(`${this.hostUrl}/file/moveFile`, params)
        .then((res) => {
          if (res.data.code === 0) {
            this.$message.error(res.data.msg);
          } else {
            let path = "";
            if (this.path.length === 0) {
              path = this.nowfolder;
            } else {
              path = this.path.join("/") + "/" + this.nowfolder;
            }
            this.$message.success(res.data.msg);
            this.$refs.upFile.setPath(path);
            this.refreshFileList(path);
            this.closebar();
            this.dialogFormVisible = false;
            this.needMoveFileId = null;
            this.newFilePath = "我的网盘";
          }
        })
        .catch((err) => {
          this.$message.error(res.data.msg);
          this.$refs.upFile.setPath(path);
          this.refreshFileList(path);
          this.closebar();
          this.dialogFormVisible = false;
          this.needMoveFileId = null;
          this.newFilePath = "我的网盘";
          console.log(err);
        });
    },
    reFileNameBox(id) {
      this.$prompt("请输入文件名", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        //inputPattern: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/,
        //inputErrorMessage: "邮箱格式不正确",
      })
        .then(({ value }) => {
          if (value) {
            this.reFileName(id, value);
          } else {
            this.$message.error("文件名禁止为空");
          }
        })
        .catch(() => {});
    },
    reFileName(id, value) {
      let params = {
        id: id,
        file_Name: value,
        uid: this.uid,
      };
      this.$http
        .post(`${this.hostUrl}/file/reName`, params)
        .then((res) => {
          if (res.data.code === 0) {
            this.$message.error(res.data.msg);
          } else {
            let path = "";
            if (this.path.length === 0) {
              path = this.nowfolder;
            } else {
              path = this.path.join("/") + "/" + this.nowfolder;
            }
            this.$message.success(res.data.msg);
            this.$refs.upFile.setPath(path);
            this.refreshFileList(path);
            this.closebar();
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    handleDelete(index, row) {
      this.$confirm("此操作将永久删除该文件, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        closeOnClickModal: false,
        lockScroll: false,
        type: "warning",
      })
        .then(() => {
          this.$http
            .delete(`${this.hostUrl}/file/delete/${row.hash_name}/${row.id}`)
            .then((res) => {
              this.$message.success(res.data.msg);
              let path = "";
              if (this.path.length === 0) {
                path = this.nowfolder;
              } else {
                path = this.path.join("/") + "/" + this.nowfolder;
              }
              this.$refs.upFile.setPath(path);
              this.refreshFileList(path);
              this.closebar();
            })
            .catch((err) => {
              console.log("Error=>", err);
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
    handleShare(index, row) {
      let url = `${this.hostUrl}/file/download/${row.id}/${row.hash_name}`;
      this.$confirm(
        `<p style="word-wrap: break-word; word-break: break-all;">${url}</p>`,
        "点击确定复制以下链接分享给好友哦~",
        {
          dangerouslyUseHTMLString: true,
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "success",
        }
      )
        .then(() => {
          this.$copyText(url).then(
            (e) => {
              this.$message({
                type: "success",
                message: "已复制到剪切板!",
              });
            },
            (e) => {
              this.$message({
                type: "error",
                message: "复制失败!",
              });
            }
          );
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消分享!",
          });
        });
    },
    handleDownload() {
      let path = "";
      if (this.path.length === 0) {
        path = this.nowfolder;
      } else {
        path = this.path.join("/") + "/" + this.nowfolder;
      }
      this.$refs.upFile.setPath(path);
      setTimeout(() => {
        this.refreshFileList(path);
        this.closebar();
      }, 1000);
    },
    refreshFileList(path) {
      this.getFileList(path);
    },
    getFileList(path) {
      let params = {
        uid: sessionStorage.getItem("uid"),
        path: path,
      };
      this.uid = sessionStorage.getItem("uid");
      this.$http
        .post(`${this.hostUrl}/file/list`, params)
        .then((res) => {
          if (res.data.code === 0) {
            this.$message.error(res.data.msg);
          } else {
            this.tableData = res.data;
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    getFile(data) {
      let url = `${this.hostUrl}/file/download/${data.id}/${data.hash_name}`;
      return url;
    },
    dealSize(row, column) {
      if (0 < row.size && row.size < 102.4) {
        return `${row.size}byte`;
      } else if (102.4 <= row.size && row.size < 1024 * 1024) {
        let kb = (row.size / 1024).toFixed(2);
        return `${kb}Kb`;
      } else if (1024 * 1024 <= row.size && row.size < 1024 * 1024 * 1024) {
        let mb = (row.size / 1024 / 1024).toFixed(2);
        return `${mb}Mb`;
      } else if (
        1024 * 1024 * 1024 <= row.size &&
        row.size < 1024 * 1024 * 1024 * 1024
      ) {
        let gb = (row.size / 1024 / 1024 / 1024).toFixed(2);
        return `${gb}Gb`;
      }
    },
    dealTime(row, column) {
      return this.formatTime(row.upload_time);
    },
    formatTime(value) {
      var date = new Date(value);
      var Y = date.getFullYear();
      var M =
        date.getMonth() + 1 < 10
          ? `0${date.getMonth() + 1}`
          : date.getMonth() + 1;
      var D = date.getDate() < 10 ? `0${date.getDate()}` : date.getDate();
      var h = date.getHours() < 10 ? `0${date.getHours()}` : date.getHours();
      var m =
        date.getMinutes() < 10 ? `0${date.getMinutes()}` : date.getMinutes();
      var s =
        date.getSeconds() < 10 ? `0${date.getSeconds()}` : date.getSeconds();
      return `${Y}-${M}-${D} ${h}:${m}:${s}`;
    },
  },
  mounted() {
    let protocol = window.location.protocol; //协议
    let host = window.location.host; //主机
    this.hostUrl = `${protocol}//${host}`;
    const that = this;
    window.onresize = () => {
      return (() => {
        window.fullHeight = document.documentElement.clientHeight;
        that.fullHeight = window.fullHeight;
      })();
    };
    this.height = this.fullHeight - 290;
    this.getFileList("我的网盘");
    this.$refs.upFile.setPath("我的网盘");
  },
};
</script>
